.backdrop {
    z-index: 4;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(44, 36, 36, .7);
    animation: appear-backdrop .5s ease-out forwards;
}

@keyframes appear-backdrop {
    from {
        opacity: 0;
    }

    to {
        opacity: 1
    }
}